  opacity: 0.85;
  position: absolute;
  bottom: 45px;
  right: 104px;
  width: 100px;
  height: 100px;
  -webkit-perspective: 190px;
          perspective: 190px;
}
.cube {
  height: 80px;
  width: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  -webkit-animation: rotate3d 16s linear infinite;
          animation: rotate3d 16s linear infinite;
}

.top_s,
.bot,
.front,
.back,
.side,
.side2 {
  box-shadow: 1px 1px 25px rgba(255, 130, 90, 1);
  border: 3px double black;
  opacity: 0.75;
  height: 125%;
  width: 135%;
  padding: 0 0;
  position: absolute;
  z-index: 2;
}

.front {
  -webkit-transform: rotateY(0deg) translateZ(40px);
          transform: rotateY(0deg) translateZ(40px);
}
.back {
  -webkit-transform: rotateY(180deg) translateZ(40px);
          transform: rotateY(180deg) translateZ(40px);
}
.side {
  -webkit-transform: rotateY(90deg) translateZ(40px);
          transform: rotateY(90deg) translateZ(40px);
}
.side2 {
  -webkit-transform: rotateY(-90deg) translateZ(40px);
          transform: rotateY(-90deg) translateZ(40px);
}
.top_s {
  -webkit-transform: rotateX(90deg) translateZ(40px);
          transform: rotateX(90deg) translateZ(40px);
}
.bot {
  -webkit-transform: rotateX(-90deg) translateZ(40px);
          transform: rotateX(-90deg) translateZ(40px);
}

.left,
.top,
.corner {
  visibility: hidden;
}
.left {
  opacity: 0.5;
  background-size: 150% 20vh;
  background-repeat: repeat-y;
  background-image: url(/static/media/left.a7a0f959.png);
  position: absolute;
  top: 20vh;
  height: calc(100% - 20vh);
  width: 6.5vw;
  z-index: 0;
}
.top {
  opacity: 0.5;
  background-size: 20vw 120%;
  background-repeat: repeat-x;
  background-image: url(/static/media/top.c61d3a1e.png);
  position: absolute;
  top: 0px;
  left: 10vw;
  width: calc(100% - 10vw);
  height: 20vh;
  z-index: 0;
}

::-webkit-scrollbar {
